@font-face {
  font-family: GT America;
  src: url("GT-America-Standard-Regular.80e0db52.otf") format("opentype");
}

@font-face {
  font-family: GT America Bold;
  src: url("GT-America-Standard-Bold.004e54b8.otf") format("opentype");
}

@font-face {
  font-family: GT America Condensed;
  src: url("GT-America-Condensed-Regular.7a423f65.otf") format("opentype");
}

body {
  color: #eee;
  -font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  font-family: GT America;
  font-size: 13px;
}

h3.title {
  text-align: center;
  margin-top: 0;
  margin-bottom: 20px;
  font-family: GT America Bold;
  font-size: 18px;
  line-height: 28px;
}

.container {
  max-width: 600px;
  margin: auto;
}

.hidden {
  display: none;
}

.footnote {
  color: #ffffffbf;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  font-family: GT America;
  font-size: 12px;
}

.footnote.graph-footnote {
  margin-top: 100px;
}

.footnote.map-footnote {
  margin-top: 0;
}

.mobile-only {
  display: none;
}

.btn-text {
  color: #fff;
  text-align: center;
  border-radius: 3px;
  font-family: GT America Bold;
  font-size: 11px;
  font-weight: 600;
}

.outliers-btn {
  cursor: pointer;
  user-select: none;
  text-transform: uppercase;
  border: 1px solid #333;
  margin-left: 1em;
  padding: 10px 15px;
}

.outliers-btn.showing {
  color: #fff;
  background-color: #1d1d1d;
}

.switch-container {
  cursor: pointer;
  user-select: none;
  justify-content: center;
  margin: 1.5em 0;
  display: flex;
}

.switch-container .switch {
  text-transform: uppercase;
  border: 1px solid #333;
  width: 130px;
  padding: 10px;
}

.switch.left {
  border-radius: 3px 0 0 3px;
}

.switch.right {
  border-left: 0;
  border-radius: 0 3px 3px 0;
}

.switch-container .switch.showing, .switch-container .switch:hover {
  background-color: #1d1d1d;
}
